$features-title: #000000;
$features-item: #000000;

.features {
  margin: 0 auto;
  padding: 0 30px;

  @include for-size (tablet) {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 46px 50px 53px 50px;
  }

  @include for-size (desktop) {
    max-width: #{$general-width + 100px};
    margin: 0 auto;
    padding: 62px 20px 60px 225px;
    box-sizing: border-box;
  }

  &__title {
    margin: 0;
    padding-top: 3px;
    color: $features-title;
    font-size: 25px;
    font-weight: bold;
    line-height: 30px;

    @include for-size (tablet) {
      padding-top: 2px;
      padding-bottom: 15px;
      font-size: 43px;
      line-height: 50px;
    }

    @include for-size (desktop) {
      padding: 5px 0 12px;
      font-size: 53px;
      line-height: 57px;
    }
  }

  &__list {
    margin: 0;
    padding: 0;

    @include for-size (tablet) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr 1fr;
    }

    @include for-size (desktop) {
      grid-template-columns: 350px auto;
    }
  }


  &__item {
    padding: 83px 0 0 0;
    color: $features-item;
    font-size: 17px;
    font-weight: bold;
    line-height: 20px;
    list-style-type: none;

    @include for-size (tablet) {
      width: 300px;
      font-size: 20px;
      line-height: 22px;
      margin-bottom: 28px;
    }

