    }

    &--rocket {
      grid-column: 2 / 3;
      grid-row: 2 / 3;
      padding-left: 10px;
      margin-left: -10px;
      background: url("data:image/svg+xml,%3Csvg width='55' height='23' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M40.947 5.75A47.823 47.823 0 0028.41 4.065c-2.932 0-5.56.198-7.785.594L13.75 0H.101l5.864 6.147h8.493c-4.651 1.586-6.875 3.47-6.875 3.47v3.767s2.325 1.883 6.976 3.47H5.864L0 23h13.649l6.875-4.66c2.325.397 4.853.595 7.785.595 4.55 0 8.796-.694 12.537-1.685-1.214-1.784-1.921-3.668-1.921-5.85.1-1.982.91-3.865 2.022-5.65zM44.485 6.741c-1.314 1.586-2.022 3.173-2.022 4.759 0 1.586.708 3.172 2.022 4.759C50.955 14.077 55 11.499 55 11.499s-4.044-2.577-10.515-4.758z' fill='%2363D1BB'/%3E%3C/svg%3E") 4px 31px no-repeat;
