@import "media";
@import "fonts";
@import "_blocks/button.scss";
@import "_blocks/link.scss";
@import "_blocks/header.scss";
@import "_blocks/footer.scss";
@import "_blocks/catalog.scss";
@import "_blocks/comment.scss";
@import "_blocks/contacts.scss";
@import "_blocks/features.scss";
@import "_blocks/hot-product.scss";
@import "_blocks/intro.scss";
@import "_blocks/overview.scss";
@import "_blocks/form.scss";
@import "_blocks/modal";

$body-background-color: #ffffff;

.head {
  height: 100%;
}

.body {
  display: grid;
  align-content: start;
  min-height: 100%;
  min-width: 320px;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  background-color: $body-background-color;
}


.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.img {
  max-width: 100%;
  height: auto;
  @include for-size (phone-only) {
    max-width: 50%;
  }
}

.br-phone-only {
  @include for-size(tablet) {
    display: none;
  }
}
